'use client';

import React, { useCallback } from 'react';
import { useDropzone } from 'react-dropzone';
import { Upload, X } from 'lucide-react';
import Image from 'next/image';

interface ImageUploadProps {
    onImageSelected: (file: File) => void;
    selectedImage: File | null;
    onClear: () => void;
}

const ImageUpload: React.FC<ImageUploadProps> = ({ onImageSelected, selectedImage, onClear }) => {
    const onDrop = useCallback((acceptedFiles: File[]) => {
        if (acceptedFiles && acceptedFiles.length > 0) {
            onImageSelected(acceptedFiles[0]);
        }
    }, [onImageSelected]);

    const { getRootProps, getInputProps, isDragActive } = useDropzone({
        onDrop,
        accept: {
            'image/*': []
        },
        multiple: false
    });

    if (selectedImage) {
        return (
            <div className="relative w-full h-64 bg-gray-100 rounded-lg overflow-hidden border border-gray-300 flex items-center justify-center">
                <Image
                    src={URL.createObjectURL(selectedImage)}
                    alt="Selected"
                    width={400}
                    height={300}
                    className="object-contain max-h-full"
                />
                <button
                    onClick={(e) => { e.stopPropagation(); onClear(); }}
                    className="absolute top-2 right-2 p-1 bg-white rounded-full shadow hover:bg-gray-100"
                >
                    <X className="w-5 h-5 text-gray-600" />
                </button>
            </div>
        );
    }

    return (
        <div
            {...getRootProps()}
            className={`w-full h-64 border-2 border-dashed rounded-lg flex flex-col items-center justify-center cursor-pointer transition-colors
        ${isDragActive ? 'border-blue-500 bg-blue-50' : 'border-gray-300 hover:border-gray-400 bg-gray-50'}`}
        >
            <input {...getInputProps()} />
            <Upload className="w-10 h-10 text-gray-400 mb-2" />
            <p className="text-sm text-gray-500 font-medium">
                {isDragActive ? "Drop the image here..." : "Drag & drop an image, or click to select"}
            </p>
            <p className="text-xs text-gray-400 mt-1">Supports JPG, PNG, WEBP</p>
        </div>
    );
};

export default ImageUpload;
