import axios from 'axios';

const api = axios.create({
    baseURL: 'http://localhost:8000/api/v1',
    headers: {
        'Content-Type': 'application/json',
    },
});

export const analyzeImage = async (file: File) => {
    const formData = new FormData();
    formData.append('file', file);
    const response = await api.post('/agent/analyze-image', formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
        },
    });
    return response.data;
};

export const recommend = async (preferences: any, currentContext: any) => {
    const response = await api.post('/agent/recommend', {
        user_preferences: preferences,
        current_item_context: currentContext,
    });
    return response.data;
};

export default api;
