'use client';

import { useState } from 'react';
import ImageUpload from '@/components/ImageUpload';
import { analyzeImage } from '@/lib/api';
import { Loader2, Sparkles, Search } from 'lucide-react';

export default function Home() {
  const [selectedImage, setSelectedImage] = useState<File | null>(null);
  const [prompt, setPrompt] = useState('');
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [analysisResult, setAnalysisResult] = useState<any>(null);

  const handleImageSelected = (file: File) => {
    setSelectedImage(file);
    setAnalysisResult(null);
  };

  const handleAnalyze = async () => {
    if (!selectedImage) return;

    setIsAnalyzing(true);
    try {
      const result = await analyzeImage(selectedImage);
      setAnalysisResult(result);
    } catch (error) {
      console.error("Analysis failed:", error);
      alert("Failed to analyze image.");
    } finally {
      setIsAnalyzing(false);
    }
  };

  return (
    <main className="min-h-screen bg-gradient-to-br from-current to-gray-800 text-white p-8">
      <div className="max-w-4xl mx-auto space-y-8">

        {/* Header */}
        <div className="text-center space-y-2">
          <div className="flex items-center justify-center gap-2">
            <Sparkles className="w-8 h-8 text-yellow-400" />
            <h1 className="text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-500">
              Shopping Suggester
            </h1>
          </div>
          <p className="text-gray-400">Upload an image or describe what you're looking for.</p>
        </div>

        {/* Input Section */}
        <div className="grid md:grid-cols-2 gap-8">
          {/* Image Upload */}
          <div className="bg-gray-800/50 p-6 rounded-2xl border border-gray-700/50 backdrop-blur-sm shadow-xl">
            <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
              <span>ðŸ“¸</span> Image Search
            </h2>
            <ImageUpload
              selectedImage={selectedImage}
              onImageSelected={handleImageSelected}
              onClear={() => setSelectedImage(null)}
            />

            {selectedImage && !analysisResult && (
              <button
                onClick={handleAnalyze}
                disabled={isAnalyzing}
                className="mt-4 w-full py-3 bg-blue-600 hover:bg-blue-700 rounded-lg font-medium transition-colors flex items-center justify-center gap-2"
              >
                {isAnalyzing ? <Loader2 className="animate-spin" /> : "Analyze Image"}
              </button>
            )}
          </div>

          {/* Text Prompt */}
          <div className="bg-gray-800/50 p-6 rounded-2xl border border-gray-700/50 backdrop-blur-sm shadow-xl flex flex-col">
            <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
              <span>ðŸ’¬</span> Describe Item
            </h2>
            <textarea
              value={prompt}
              onChange={(e) => setPrompt(e.target.value)}
              placeholder="I'm looking for a vintage leather jacket that costs under $100..."
              className="w-full flex-1 bg-gray-900/50 border border-gray-700 rounded-lg p-4 text-gray-200 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none min-h-[150px]"
            />
            <button
              className="mt-4 w-full py-3 bg-purple-600 hover:bg-purple-700 rounded-lg font-medium transition-colors flex items-center justify-center gap-2"
            >
              <Search className="w-4 h-4" /> Find Suggestions
            </button>
          </div>
        </div>

        {/* Results Section (Placeholder) */}
        {analysisResult && (
          <div className="bg-gray-800/50 p-6 rounded-2xl border border-gray-700/50 backdrop-blur-sm shadow-xl animate-in fade-in slide-in-from-bottom-4">
            <h2 className="text-xl font-semibold mb-4 text-green-400">Analysis Result</h2>
            <div className="space-y-2">
              <p><span className="text-gray-400">Detected:</span> <span className="font-medium text-lg">{analysisResult.item_name}</span></p>
              <p className="text-gray-300">{analysisResult.description}</p>
              <div className="flex flex-wrap gap-2 mt-2">
                {analysisResult.detected_keywords.map((kw: string, i: number) => (
                  <span key={i} className="px-3 py-1 bg-gray-700 rounded-full text-sm text-gray-300">#{kw}</span>
                ))}
              </div>
            </div>
          </div>
        )}

      </div>
    </main>
  );
}
